
import Blockies from '@/helpers/blockies';
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'MewBlockie',
  props: {
    /**
     * Currency image url
     */
    currency: {
      type: String,
      default: '',
    },
    /**
     * Valid address
     */
    address: {
      type: String,
      default: '',
    },
    /**
     * Blockie width
     */
    width: {
      type: String,
      default: '64px',
    },
    /**
     * Blockie height
     */
    height: {
      type: String,
      default: '64px',
    },
    /**
     * Remove inset shadow
     */
    flat: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      scale: 16,
      size: 8,
      blockieImg: '',
    };
  },
  watch: {
    address() {
      this.createBlockie();
    },
    width() {
      this.createBlockie();
    },
    height() {
      this.createBlockie();
    },
    scale() {
      this.createBlockie();
    },
    size() {
      this.createBlockie();
    },
  },
  mounted() {
    this.createBlockie();
  },
  methods: {
    createBlockie() {
      this.blockieImg = Blockies({
        seed: this.address ? this.address.toLowerCase() : '',
        size: this.size,
        scale: this.scale,
      }).toDataURL();
      const blockieElem = this.$refs.blockie as HTMLImageElement;
      blockieElem.style.width = this.width;
      blockieElem.style.height = this.height;
    },
  },
});
